

































































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import RpnCalc from '@/typescript/rpn/RpnCalc.ts';

@Component({
  components: {
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '逆ポーランド記法',
    },
  },
})
export default class Rpn extends Vue {
  // Data
  private dataButtonText: string = '解答と処理';
  private dataFormula: string = '';
  private dataProcesses: {
    preText: string,
    processingChar: string,
    postText: string,
    stacks: number[],
  }[] = [];
  private dataAnswer: string = '';
  private dataConvertedFormula: string = '';
  private dataConvertErrorMessage: string = '';
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'simple',
      caption: '1 2 3-+',
      content: '1 2 3-+',
    },
    {
      name: 'simple2',
      caption: '1 2+ 3- 4* 5^ 6/',
      content: '1 2+ 3- 4* 5^ 6/',
    },
    {
      name: 'complex',
      caption: '1 2 3* 4- 6 20 10//*+',
      content: '1 2 3* 4- 6 20 10//*+',
    },
    {
      name: 'seconds',
      caption: '年間の秒数',
      content: '365 24 60 60***',
    },
  ];
  private dataSampleIndex: number = 0;

  // Methods
  private listenOnSampleSelect($event: Event): void {
    this.dataFormula = this.dataSamples[Number($event)].content;
  }
  private listenInputText($event: Event): void {
    this.dataFormula = (<HTMLInputElement>(<any>$event).target).value;
  }
  private solve(): void {
    this.dataProcesses.length = 0;
    this.dataAnswer = '';
    let stacks: number[] = [];
    let signs: string[] = RpnCalc.analyzeFormula(this.dataFormula);
    this.convert(signs);
    for (let i: number = 0; i < signs.length; i++) {
      // Calc
      if (signs[i].match(/^[0-9]+$/)) {
        stacks.push(Number(signs[i]));
      } else {
        if (2 <= stacks.length) {
          let num2: number = <number>stacks.pop();
          let num1: number = <number>stacks.pop();
          switch (signs[i]) {
            case '+':
              stacks.push(num1 + num2);
              break;
            case '-':
              stacks.push(num1 - num2);
              break;
            case '*':
              stacks.push(num1 * num2);
              break;
            case '/':
              if (num2 === 0) {
                this.dataAnswer = 'エラーです。0除算が発生しました。';
              } else {
                stacks.push(num1 / num2);
              }
              break;
            case '^':
              stacks.push(num1 ** num2);
              break;
            case '%':
              if (num2 === 0) {
                this.dataAnswer = 'エラーです。0除算が発生しました。';
              } else {
                stacks.push(num1 % num2);
              }
              break;
          }
        } else {
          this.dataAnswer = '式が正しくありません。';
        }
      }
      if (this.dataAnswer !== '') {
        break;
      }
      let process: {
        preText: string,
        processingChar: string,
        postText: string,
        stacks: number[],
      } = {
        preText: 0 < i ? signs.slice(0, i).join(' ') : '',
        processingChar: signs[i],
        postText: i + 1 < signs.length ? signs.slice(i + 1).join(' ') : '',
        stacks: stacks.slice(0),
      }
      this.dataProcesses.push(process);
    }
    if (this.dataAnswer === '') {
      if (stacks.length === 1) {
        this.dataAnswer = String(stacks.pop());
      } else {
        this.dataAnswer = '式が正しくありません。';
      }
    }
  }
  private convert(signs: string[]): void {
    this.dataConvertedFormula = RpnCalc.convertFromRpn(signs);
    this.dataConvertErrorMessage = RpnCalc.errorMessage;
  }
}
