export default class RpnCalc {
  private static _errorMessage: string = '';

  public static get errorMessage(): string {
    return RpnCalc._errorMessage;
  }

  public static convertFromRpn(signs: string[]) {
    RpnCalc._errorMessage = '';
    let formula: string = '';
    let stacks: string[] = [];
    for (let i: number = 0; i < signs.length; i++) {
      if (signs[i].match(/^[0-9]+$/)) {
        stacks.push(signs[i]);
      } else {
        if (signs[i].match(/^[0-9]+$/)) {
          stacks.push(signs[i]);
        } else {
          if (2 <= stacks.length) {
            let num2: string = <string>stacks.pop();
            let num1: string = <string>stacks.pop();
            switch (signs[i]) {
              case '+':
              case '-':
              case '*':
              case '/':
              case '^':
              case '%':
                stacks.push('(' + num1 + ' ' + signs[i] + ' ' + num2 + ')');
            }
          } else {
            RpnCalc._errorMessage = '式が正しくありません。';
          }
        }
        if (RpnCalc._errorMessage !== '') {
          break;
        }
      }
    }
    if (RpnCalc._errorMessage === '') {
      if (stacks.length === 1) {
        formula = stacks[0].replace(/(^\(|\)$)/g, '');
      } else {
        RpnCalc._errorMessage = '式が正しくありません。';
      }
    }
    return formula;
  }

  public static calc(signs: string[]): number {
    RpnCalc._errorMessage = '';
    let answer: number = 0;
    let stacks: number[] = [];
    for (let i: number = 0; i < signs.length; i++) {
      if (signs[i].match(/^[0-9]+$/)) {
        stacks.push(Number(signs[i]));
      } else {
        if (signs[i].match(/^[0-9]+$/)) {
          stacks.push(Number(signs[i]));
        } else {
          if (2 <= stacks.length) {
            let num2: number = <number>stacks.pop();
            let num1: number = <number>stacks.pop();
            switch (signs[i]) {
              case '+':
                stacks.push(num1 + num2);
                break;
              case '-':
                stacks.push(num1 - num2);
                break;
              case '*':
                stacks.push(num1 * num2);
                break;
              case '/':
                if (num2 === 0) {
                  RpnCalc._errorMessage = 'エラーです。0除算が発生しました。';
                } else {
                  stacks.push(num1 / num2);
                }
                break;
              case '^':
                stacks.push(num1 ** num2);
                break;
              case '%':
                if (num2 === 0) {
                  RpnCalc._errorMessage = 'エラーです。0除算が発生しました。';
                } else {
                  stacks.push(num1 % num2);
                }
                break;
            }
          } else {
            RpnCalc._errorMessage = '式が正しくありません。';
          }
        }
        if (RpnCalc._errorMessage !== '') {
          break;
        }
      }
    }
    if (RpnCalc._errorMessage === '') {
      if (stacks.length === 1) {
        answer = <number>stacks.pop();
      } else {
        RpnCalc._errorMessage = '式が正しくありません。';
      }
    }
    return answer;
  }

  public static analyzeFormula(formula: string): string[] {
    let signs: string[] = [];
    let sign: string = '';
    for (let i: number = 0; i < formula.length; i++) {
      let char: string = formula.charAt(i);
      if (char.match(/[\+\-\*\/\^\%]/)) {
        if (sign !== '') {
          signs.push(sign);
          sign = '';
        }
        signs.push(char);
      } else if (char.match(/[0-9]/)) {
        sign += char;
      } else {
        if (sign !== '') {
          signs.push(sign);
          sign = '';
        }
      }
    }
    return signs;
  }
}