









import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CButton extends Vue {
  // Props
  @Prop() public buttonText!: string | undefined;

  // Data
  private dataButtonText: string | undefined = this.buttonText;
  private dataPosition: string = '';

  // Watch
  @Watch('dataPosition')
  onValueChange(newValue: string, oldValue: string): void {
  }

  // Emit
  @Emit('clickButton')
  private emitClickButton(): void {}

  // Methods
  private listenClick($event: Event): void {
    const TIMEOUT: number = 512;
    const RIPPLE_ELEM: HTMLInputElement = (<HTMLInputElement>(<any>$event).target);
    let rippleWidth: number = RIPPLE_ELEM.offsetWidth;
    let rippleRect: ClientRect | DOMRect = RIPPLE_ELEM.getBoundingClientRect();
    let left: number = Number((<any>$event).pageX) - rippleRect.left - window.pageXOffset - (rippleWidth / 2);
    let top: number = Number((<any>$event).pageY) - rippleRect.top - window.pageYOffset - (rippleWidth / 2);
    let position: string = '';
    position += 'left:' + left + 'px;';
    position += 'top:' + top + 'px;';
    position += 'width:' + rippleWidth + 'px;';
    position += 'height:' + rippleWidth + 'px;';
    this.dataPosition = position;
    setTimeout((): void => {
      this.dataPosition = '';
    }, TIMEOUT);
    this.emitClickButton();
  }
}
